@tailwind base;
@tailwind components;
@tailwind utilities;


.app {
  background-color: rgb(231, 241, 239);
}
/* Header Component Css */
.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 5px 5px 10px rgb(165, 187, 182);
}
.logo {
  width: 120px;
  border-radius: 40px;
}
/* when 2 values given 1st value represents top and bottom , 2nd value represents left and right */
/* when 4 values given top right bottom left*/
.nav-items {
  padding: 10px 20px 0px 20px;
}
.nav-items > ul {
  display: flex;
  list-style-type: none;
  font-size: 20px;
}
.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}
.btn-login {
  cursor: pointer;
}

/* Body Component Css */
.body {
  padding: 0px 20px;
}
.search-rate {
  display: flex;
  margin: 10px;
}
.input-field {
  margin: 10px;
}
.search-button {
  cursor: pointer;
  margin: 10px 10px 10px 0px;
}
.Resto-Rate-Btn {
  cursor: pointer;
  margin: 10px;
}
.resto-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* RestaurantCard Component Css */

.res-image {
  width: 100%;
  border-radius: 5px;
}
.resto-card {
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  background-color: white;
  width: 250px;
  height: auto;
  /* flex-basis: calc(100% / 3); */
}
.resto-card:hover {
  cursor: pointer;
  border: 1px solid black;
  box-shadow: 5px 5px 10px rgb(165, 187, 182);
}

/* Shimmer Component Css */

.shimmer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.shimmer-card {
  margin: 10px;
  padding: 20px;
  width: 200px;
  height: 150px;
  border-radius: 10px;
  background-color: rgb(236, 245, 244);
}
.shimmer-tag1 {
  margin-top: 80px;
  width: 100px;
  height: 10px;
  border-radius: 5px;
  background-color: rgb(221, 229, 230);
}
.shimmer-tag2 {
  margin-top: 30px;
  width: 50px;
  height: 10px;
  border-radius: 5px;
  background-color: rgb(221, 229, 230);
}

/* Restaurant - Menu Component */
.menu{
    justify-content: center;
    margin: 10px;
}
.menu-container{
    justify-self: center;
}
.restaurant-tab-container{
    width: 800px;
    height: 150px;
    padding: 0px 0px 20px 0px;
    box-shadow: 5px 5px 10px rgb(165, 187, 182);
    border: 1px solid rgb(223, 223, 231);
    border-radius: 10px;
    background-color: white;
}
.error-msg{
    background-color: #f2c0c8;
}
.error-msg >h4{
    margin: 1px 0px 0px 5px;
    padding: 10px;
    color: rgb(104, 101, 101);
}
.resto-tab-1{
    display: flex;
    padding: 10px;
}
.resto-tab-1 > h4{
    margin: 0px 0px 0px 3px;
}
.dot{
    margin: 0px 0px 5px 5px;
    font-size: 16px;
    color: rgba(2, 6, 12, 0.45);
}
.resto-tab-2{
    padding: 0px 10px 10px 10px;
}
.resto-tab-2 > h4{
    margin: 0px 0px 0px 3px;
    color: rgb(255, 82, 0);
    text-decoration: underline;
}
.resto-tab-3{
    padding: 0px 10px 10px 10px;
}
.resto-tab-3 > h4{
    margin: 0px 0px 0px 3px;
}


/* Offers Component */
.offers{
    display: flex;
    /* flex-direction: column; */
}
.offers-content{
    background-color: white;
    width: 300px;
    padding: 20px;
    margin: 10px;
    border-radius: 20px;
    border: 1px solid rgba(2, 6, 12, 0.15);
    display: flex;
}
.offer-image{
    width: 50px;
    height: 50px;
}
.offer-percent >h5{
    margin: 0px 0px 5px 5px;
}

/*  Collapse Component  */

.item-description{
    display: flex;
    justify-content: space-between;
    margin: 1px;
    border-bottom: 1px solid #d3d3d3;
}
.item-description >h4{
    margin: 0px;
}
.item-image{
    margin: 5px;
    width: 156px;
    height: 144px;
    border-radius: 25px;
}